<template>
    <uc class="m-dashboard-email">
        <div class="m-profile-email">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 504.124 504.124" v-bind:class="'u-pic'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M0 200.862L252.062 51.2l252.062 149.662v220.554H0V200.862z" fill="#d07c40"/><path d="M55.138 11.815h393.846c8.665 0 15.754 7.089 15.754 15.754v275.692c0 8.665-7.089 15.754-15.754 15.754H55.138c-8.665 0-15.754-7.089-15.754-15.754V27.569c.001-8.664 7.09-15.754 15.754-15.754z" fill="#efefef"/><path d="M415.902 43.323l25.206-31.508h7.877c8.665 0 15.754 7.089 15.754 15.754v15.754h-48.837zm-204.8 0l25.206-31.508h55.138L266.24 43.323h-55.138zm-171.717 0V27.569c0-8.665 7.089-15.754 15.754-15.754h39.385L69.317 43.323H39.385z" fill="#26a6d1"/><path d="M504.123 468.677c0 12.997-10.634 23.631-23.631 23.631H23.631C10.634 492.309 0 481.674 0 468.677V200.862l252.062 110.277 252.062-110.277v267.815h-.001z" fill="#efc75e"/><path d="M504.123 468.677c0 12.997-10.634 23.631-23.631 23.631H23.631C10.634 492.309 0 481.674 0 468.677v-47.262l504.123-220.554v267.816z" fill="#e8c15b"/><path d="M160.295 151.631l3.151 16.542-25.206-39.385-35.446 55.138c-.394 5.12 22.449 9.058 50.806 9.058s51.2-3.938 51.2-9.058l-31.114-46.08-13.391 13.785zm3.151-21.662c5.514 0 9.846-4.332 9.846-9.846s-4.332-9.846-9.846-9.846-9.846 4.332-9.846 9.846 4.332 9.846 9.846 9.846zm80.739-3.938h157.538c4.332 0 7.877-3.545 7.877-7.877s-3.545-7.877-7.877-7.877H244.185c-4.332 0-7.877 3.545-7.877 7.877s3.544 7.877 7.877 7.877zm157.538 15.754H244.185c-4.332 0-7.877 3.545-7.877 7.877s3.545 7.877 7.877 7.877h157.538c4.332 0 7.877-3.545 7.877-7.877 0-4.333-3.545-7.877-7.877-7.877zm-39.385 31.507H244.185c-4.332 0-7.877 3.545-7.877 7.877s3.545 7.877 7.877 7.877h118.154c4.332 0 7.877-3.545 7.877-7.877-.001-4.332-3.545-7.877-7.878-7.877z" fill="#d6d9db"/><path d="M313.502 43.323l25.206-31.508h55.138L368.64 43.323h-55.138zm-204.8 0l25.206-31.508h55.138L163.84 43.323h-55.138z" fill="#e2574c"/></svg>

            <!-- 已绑定 -->
            <div v-if="status == true" class="u-done">
                <template v-if="verified == true">
                    <h1 class="u-title">已绑定邮箱</h1>
                    <p class="u-address">{{ address }}</p>
                    <el-alert
                        class="u-tip"
                        title="已验证邮箱"
                        type="success"
                        description="仅绑定邮箱用户支持邮件订阅通知等功能"
                        show-icon
                        :closable="false"
                    >
                    </el-alert>
                    <div class="u-btngroup">
                        <el-button
                            type="primary"
                            class="u-button"
                            @click="changeEmail"
                            icon="el-icon-edit"
                            >修改邮箱</el-button
                        >
                    </div>
                </template>

                <template v-if="verified == false">
                    <h1 class="u-title">未验证邮箱</h1>
                    <p class="u-address">{{ address }}</p>
                    <el-alert
                        title="未验证邮箱"
                        class="u-tip"
                        type="warning"
                        description="请尽快进行邮箱验证,否则您的账号将面临风险与权限受阻"
                        show-icon
                        :closable="false"
                    >
                    </el-alert>
                    <div class="u-btngroup">
                        <el-button
                            type="primary"
                            class="u-button"
                            @click="verify"
                            icon="el-icon-s-promotion"
                            >验证邮箱</el-button
                        >
                        <el-button
                            type="primary"
                            class="u-button"
                            @click="changeEmail"
                            icon="el-icon-edit"
                            >修改邮箱</el-button
                        >
                    </div>
                </template>
            </div>

            <!-- 未绑定 -->
            <div v-if="status == false" class="u-none">
                <h1 class="u-title">
                    {{ changeEmailMode ? "修改邮箱" : "未绑定邮箱" }}
                </h1>

                <div class="u-email">
                    <el-input
                        class="u-text u-email"
                        v-model="email"
                        placeholder="邮箱地址"
                        minlength="3"
                        maxlength="50"
                        @change="checkEmail"
                    >
                        <template slot="prepend">
                            <svg class="octicon octicon-mail" viewBox="0 0 14 16" width="14" height="16" aria-hidden="true" v-bind:class="'i-mail'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M0 4v8c0 .55.45 1 1 1h12c.55 0 1-.45 1-1V4c0-.55-.45-1-1-1H1c-.55 0-1 .45-1 1zm13 0L7 9 1 4h12zM1 5.5l4 3-4 3v-6zM2 12l3.5-3L7 10.5 8.5 9l3.5 3H2zm11-.5l-4-3 4-3v6z"/></svg>
                        </template>
                    </el-input>
                    <div class="u-error">
                        <el-alert
                            v-show="email_validate == false"
                            :title="email_validate_tip"
                            type="error"
                            show-icon
                            :closable="false"
                        ></el-alert>
                        <el-alert
                            v-show="email_available == false"
                            :title="email_available_tip"
                            type="error"
                            show-icon
                            :closable="false"
                        ></el-alert>
                    </div>
                    <i
                        v-show="ready"
                        class="el-icon-success el-icons-status"
                    ></i>
                </div>

                <el-alert
                    title="请填写正确的邮箱地址"
                    class="u-tip"
                    type="warning"
                    description="绑定邮箱后将可以使用邮箱进行登录,当第三方登录出现异常时不会受影响"
                    show-icon
                    :closable="false"
                >
                </el-alert>

                <div class="u-btngroup">
                    <el-button
                        type="primary"
                        class="u-button"
                        @click="bind"
                        :disabled="!ready"
                        >绑定邮箱</el-button
                    >
                </div>
            </div>
        </div>
    </uc>
</template>

<script>
import uc from '@/components/uc.vue'
import { validator } from "sterilizer";
import User from "@jx3box/jx3box-common/js/user";
import {
    sendVerifyEmail,
    sendBindEmail,
    checkEmailAvailable,
    checkEmailStatus,
} from "../service/profile";

export default {
    name: "email",
    props: [],
    data: function() {
        return {
            status: null,
            uid: User.getInfo().uid,
            address: "",
            verified: null,

            email: "",
            email_validate: null,
            email_validate_tip: "邮箱格式不正确或长度超出限制",
            email_available: null,
            email_available_tip: "邮箱已被使用,请更换",

            changeEmailMode: false,
        };
    },
    computed: {
        ready: function() {
            return this.email_validate && this.email_available;
        },
    },
    methods: {
        verify: function() {
            sendVerifyEmail(this.uid).then((res) => {
                this.$message({
                    message: "邮件已发送请查收",
                    type: "success",
                });
            });
        },
        checkEmail: function() {
            // 如果为空
            if (this.email == "") {
                this.email_validate = null;
                this.email_available = null;
                return;
            }

            // 校验格式
            let result = validator(this.email, {
                isEmail: true,
                len: [3, 50],
            });
            this.email_validate = result;
            if (!result) return;

            // 邮箱是否可用
            checkEmailAvailable(this.email).then((res) => {
                this.email_available = res.data.data;
            });
        },
        bind: function() {
            if (!this.ready) {
                this.$message.error("邮箱地址不合法");
                return;
            }

            sendBindEmail({
                uid: this.uid,
                email: this.email,
            }).then((res) => {
                this.$alert("申请提交成功,请前往邮箱验证", "消息", {
                    confirmButtonText: "确定",
                });
                this.status = true;
                this.verified = false;
                this.address = this.email;
            });
        },
        changeEmail: function() {
            this.status = false;
            this.changeEmailMode = true;
        },
    },
    mounted: function() {
        this.uid = User.getInfo().uid;
        checkEmailStatus().then((res) => {
            if (res.data.data.email) {
                this.status = true;
                this.address = res.data.data.email;
                this.verified = !!parseInt(res.data.data.verified);
            } else {
                this.status = false;
            }
        });
    },
    components : {
        uc
    }
};
</script>

<style lang="less">
@import "../assets/css/profile/email.less";
</style>
